@mixin btn() {
  margin: 0.5rem 0.5rem 0rem 0rem;

  padding: 0.3rem 0.5rem;
  border-radius: 5px;

  &:hover {
    box-shadow: var(--box-shadow);
  }
}

@mixin link {
  color: var(--text-link-color);
  font-weight: bold;

  &:hover {
    text-decoration: underline;
  }
}

@mixin card() {
  padding: 1rem;
  border-radius: 10px;
  background-color: var(--light-color);
  box-shadow: var(--box-shadow);
}
